import { AnalyticStepType } from '../step-tracker/analytics-step-tracker';

import PaymentProvidersTrackerService from './payment-providers-tracker-service';
import { BraintreeConnectTracker } from './trackers';
import { EventsPayload } from './types';

export default class PaymentProvidersTracker implements PaymentProvidersTrackerService {
    private braintreeConnectTracker?: BraintreeConnectTracker;

    constructor () {
        this.braintreeConnectTracker = new BraintreeConnectTracker();
    }

    checkoutBegin(): void {
        console.log('PaymentProvidersTracker: checkoutBegin');
    }

    clickPayButton(payload?: EventsPayload) {
        console.log('PaymentProvidersTracker: clickPayButton', payload);
    }

    customerEmailEntry(email?: string) {
        console.log('PaymentProvidersTracker: customerEmailEntry', email);
    }

    customerSuggestionInit(payload?: EventsPayload) {
        console.log('PaymentProvidersTracker: customerSuggestionInit', payload);
    }

    customerSuggestionExecute() {
        console.log('PaymentProvidersTracker: customerSuggestionExecute');
    }

    customerPaymentMethodExecuted(payload?: EventsPayload) {
        console.log('PaymentProvidersTracker: customerPaymentMethodExecuted', payload);
    }

    exitCheckout() {
        console.log('PaymentProvidersTracker: exitCheckout');
    }

    orderPurchased(): void {
        console.log('PaymentProvidersTracker: orderPurchased');
    }

    paymentComplete() {
        console.log('PaymentProvidersTracker: paymentComplete');
    }

    paymentRejected() {
        console.log('PaymentProvidersTracker: paymentRejected');
    }

    selectedPaymentMethod(paymentOption?: string) {
        console.log('PaymentProvidersTracker: selectedPaymentMethod', paymentOption);

        this.braintreeConnectTracker?.selectedPaymentMethod(paymentOption);
    }

    showShippingMethods() {
        console.log('PaymentProvidersTracker: showShippingMethods');
    }

    stepCompleted(currentStep?: AnalyticStepType): void {
        console.log('PaymentProvidersTracker: stepCompleted', currentStep);
    }

    stepViewed(currentStep?: AnalyticStepType): void {
        console.log('PaymentProvidersTracker: stepViewed', currentStep);
    }

    walletButtonClicked(paymentOption?: string) {
        console.log('PaymentProvidersTracker: walletButtonClicked', paymentOption);

        this.braintreeConnectTracker?.walletButtonClicked(paymentOption);
    }
}
