import { CheckoutService } from '../../checkout';

import NoopPaymentProvidersTracker from './noop-payment-providers-tracker';
import PaymentProvidersTrackerService from './payment-providers-tracker-service';
import PaymentProvidersTracker from './payment-providers-tracker';

/**
 * Creates an instance of `PaymentProvidersTrackerService`.
 *
 * @remarks
 * ```js
 * const checkoutService = createCheckoutService();
 * await checkoutService.loadCheckout();
 * const paymentProviderTracker = createPaymentProviderTracker(checkoutService);
 *
 * paymentProviderTracker.checkoutBegin();
 * ```
 *
 * @param checkoutService - An instance of CheckoutService
 * @returns an instance of `PaymentProvidersTrackerService`.
 */
export default function createPaymentProvidersTracker(
    checkoutService: CheckoutService,
): PaymentProvidersTrackerService {
    const state = checkoutService.getState();
    const paymentMethods = state.data.getPaymentMethods() || [];

    console.log('createPaymentProvidersTracker paymentMethods', paymentMethods);

    if (paymentMethods?.length > 0) {
        return new PaymentProvidersTracker();
    }

    return new NoopPaymentProvidersTracker();
}
